<template>
  <div class = "tab">
    <cube-tab-bar
    :useTransition=false
    :showSlider=true
    v-model="selectedLabel"
    :data="tabs"
    ref="tabBar"
    class="border-bottom -1px"
    >
    </cube-tab-bar>
    <div class="slide-wrapper">
      <cube-slide
        :loop=false
        :auto-play=false
        :show-dots=false
        :initial-index="index"
        ref="slide"
        :options="slideOptions"
        @change ="onchange"
      >
      <cube-slide-item v-for="(tab,index) in tabs" :key="index">
        <component :is="tab.component" :data="tab.data" ref ="component"></component>
       </cube-slide-item>
      </cube-slide>
    </div>
  </div>
</template>
<script>
import { log } from 'util';
export default {
  name:'tab',
  props:{
    tabs:{
      type:Array,
      default() {
        return {}
      }
    },
    initialIndex:{
      type:Number,
      default:0,
    }
  },
  data() {
    return {
      index: this.initialIndex,
      slideOptions: {
          listenScroll: true,
          probeType: 3,
          directionLockThreshold: 0
        }
    }
  },
  created(){
    // console.log(this.tabs)
    
  },
  computed:{
    selectedLabel: {
      get() {
        return this.tabs[this.index].label;
      },
      set(newVal) {
        this.index = this.tabs.findIndex((value) => {
          return value.label === newVal;
        });
      },
    },
  },
  mounted(){
    this.onchange(this.index)
  },
  methods:{
    onchange(current){
      this.index = current;
      const component = this.$refs.component[current]
      component.fetch && component.fetch()
    }
  }, 
  components:{
  },
};
</script>
<style lang="stylus" scoped>
  @import "~common/stylus/variable"

  .tab
    display: flex
    flex-direction: column
    height: 100%
    >>> .cube-tab
      padding: 10px 0
    .slide-wrapper
      flex: 1
      overflow: hidden
</style>