import axios from 'axios'

const urlMap = {
    development: '../../',
    production: 'http://www.lixiao55.com/ele2/'
  }
const baseUrl = urlMap[process.env.NODE_ENV]
export function get(url){
    return function(params){
        return axios.get(baseUrl + url,{
            params
        }).then((res) => {
            
            const data  = res.data;
            // console.log(data)
            // if(errno === REE_OK){
                return data;
                
            // }
        }).catch(() => {
            // console.log(e)
        })
    }
}

