<template>
  <div id="app" @touchmove.prevent>
    <v-header :seller = "this.seller" ></v-header>
    <div class="tab-wrapper">
      <tab :tabs="tabs"></tab>
    </div>
  </div>
</template>

<script>
import qs from 'query-string'
import { getSeller } from 'api'
import VHeader from 'components/v-header/v-header.vue'
import Tab from 'components/tab/tab.vue'
import Goods from 'components/Goods/goods'
import Seller from 'components/seller/seller'
import Rating from 'components/ratings/ratings'

export default {
  name: 'app',
  data(){
    return{
      seller:{},
      
    }
  },
  computed:{
    tabs(){
      return [
        {
          label: '商品',
          component:Goods,
          data:{
            seller:this.seller,
          },
        },
        {
          label:'评论',
          component:Rating,
          data:{
            seller:this.seller,
          },
        },
        {
          label:'商家',
          component:Seller,
          data:{
            seller:this.seller,
          },
        },
      ]
    },
  },
  created() {
    this._getseller()
   
    
  },
  methods: {
    _getseller(){
      getSeller().then((seller)=>{
      this.seller = seller.seller
    })
    },
  },
  components: {
    VHeader,
    Tab,
  },
}
</script>

<style lang="stylus" scoped>
  #app
    .tab-wrapper
      position: fixed
      top: 136px
      left: 0
      right: 0
      bottom: 0
</style>